/*******************************************************************************
 * Styles for the base elements of the theme.
 ******************************************************************************/

::-moz-selection { /* Code for Firefox */
  color: white;
  background: var(--global-theme-color);
}

::selection {
  color: white;
  background: var(--global-theme-color);
}

// Typography

a, table.table a {
  color: var(--global-theme-color);
  &:hover {
    color: $grey-color-dark;
    text-decoration: none;
  }
&:hover:after {
  &:hover:after :not(.nav-item.dropdown) {
    width: 100%;
  }
}
}

blockquote {
  background: var(--global-bg-color);
  border-left: 4px solid var(--global-theme-color);
  margin: 1.5em 10px;
  padding: 0.5em 10px;
  font-size: 1.2rem;
}

// Math

.equation {
  margin-bottom: 1rem;
  text-align: center;
}

// Caption

.caption {
  font-size: 0.875rem;
  margin-top: 0.75rem;
  margin-bottom: 1.5rem;
  text-align: center;
}


// Profile

.profile {
  width: 100%;

  .address {
    font-size: 0.9rem;
    margin-bottom: 5px;
    margin-top: 5px;
    font-family: monospace;
    p {
      display: inline-block;
      margin: 0;
    }
  }
}
.profile.float-right{
  margin-left: 1rem;
}
.profile.float-left{
  margin-right: 1rem;
}

@media (min-width: 576px) {
  .profile {
    width: 30%;
    .address {
      p { display: block; }
    }
  }
}
.post-description {
  margin-bottom: 2rem;
  font-size: 0.875rem;
  a {
    color: inherit;
    &:hover {
      color: var(--global-theme-color);
      text-decoration: none;
    }
  }
}




// Navbar customization

.navbar {
  box-shadow: none;
  border-bottom: 1px solid $grey-color-light;
  background-color: var(--global-bg-color);
  opacity: 0.95;
}
.navbar .dropdown-menu {
  background-color: var(--global-bg-color);
  a:not(.active) {
    color: var(--global-text-color);
  }
  a:hover {
    color: var(--global-hover-color);
  }
}
.dropdown-item {
  color: var(--global-text-color);
    &:hover {
      color: var(--global-hover-color);
      background-color: var(--global-bg-color);
    }
}
.navbar.navbar-light {
  a {
    &:hover {
      text-decoration: none;
    }
  }
  .navbar-brand {
    font-size: 1.4rem;
    color: var(--global-text-color);
  }
  .navbar-nav .nav-item .nav-link {
    color: var(--global-text-color);
    &:hover {
      color: var(--global-hover-color);
    }
  }
  .navbar-nav .nav-item.active>.nav-link {
      background-color: inherit;
      font-weight: bolder;
      color: var(--global-theme-color);
      &:hover {
        color: var(--global-hover-color);
      }
  }
  .navbar-brand.social {
    padding-bottom: 0;
    padding-top: 0;
    font-size: 1.7rem;
    a {
      i::before {
        color: var(--global-text-color);
        -webkit-transition: all 0.2s ease-in-out;
      }
      &:hover {
        i::before {
          color: var(--global-theme-color);
        }
      }
    }
  }
}


.navbar-toggler {
  .icon-bar {
    display: block;
    width: 22px;
    height: 2px;
    background-color: $grey-color-dark;
    border-radius: 1px;
    margin-bottom: 4px;
    transition: all 0.2s;
  }
  .top-bar {
    transform: rotate(45deg);
    transform-origin: 10% 10%;
  }
  .middle-bar {
    opacity: 0;
  }
  .bottom-bar {
    transform: rotate(-45deg);
    transform-origin: 10% 90%;
  }
}

.navbar-toggler.collapsed {
  .top-bar {
    transform: rotate(0);
  }
  .middle-bar {
    opacity: 1;
  }
  .bottom-bar {
    transform: rotate(0);
  }
}

#light-toggle {
  padding: 0;
  border: 0;
  background-color: inherit;
  color: var(--global-text-color);
  &:hover {
    color: var(--global-hover-color);
  }
}


// News

.news h2.year {
    color: $grey-color-light;
    border-top: 1px solid $grey-color-light;
    padding-bottom: 3rem;
    margin-top: 2rem;
    margin-bottom: -2rem;
    text-align: right;
}

.news table td {
  font-size: 1.3rem;
  font-weight: 400;
  color: var(--global-text-color);
}

.news table th {
  color: var(--global-text-color);
}



.news a {
  color: inherit;
  &:hover {
    color: var(--global-theme-color);
    text-decoration: none;
  }
}
// Social (bottom)

.social {
  text-align: center;
  padding-top: 1rem;
  .contact-icon {
    font-size: 3rem;
    a {
      color: var(--global-icon-color);
      &:hover {
        color: var(--global-theme-color);
      }
    }
  }
  .contact-note {
    font-size: 0.8rem;
  }
}


// Footer
footer.fixed-bottom {
  color: var(--global-footer-text-color);
  background-color: var(--global-footer-bg-color);
  font-size: 0.75rem;
  .container {
    padding-top: 9px;
    padding-bottom: 8px;
  }
  a {
    color: var(--global-footer-link-color);
    &:hover {
      color: pink;
      text-decoration: none;
    }
  }
}

footer.sticky-bottom {
  border-top: 1px solid $grey-color-light;
  padding-top: 40px;
  padding-bottom: 40px;
  font-size: 0.9rem;
}


// Blog

.header-bar {
  border-bottom: 1px solid var(--global-divider-color);
  text-align: center;
  padding-top: 2rem;
  padding-bottom: 3rem;
  h1 {
    color: var(--global-theme-color);
    font-size: 5rem;
  }
}

.tag-list {
  border-bottom: 1px solid var(--global-divider-color);
  text-align: center;
  padding-top: 1rem;

  ul {
    justify-content: center;
    display: flow-root;

    p, li {
      list-style: none;
      display: inline-block;
      padding: 1rem 0.5rem;
      color: var(--global-text-color-light);
    }
  }
}

.post-list {
  margin: 0;
  margin-bottom: 40px;
  padding: 0;
  li {
    border-bottom: 1px solid var(--global-divider-color);
    list-style: none;
    padding-top: 2rem;
    padding-bottom: 2rem;
    .post-meta {
      color: var(--global-text-color);
      font-size: 0.875rem;
      margin-bottom: 0;
    }
    .post-tags {
      color: var(--global-text-color);
      font-size: 0.875rem;
      padding-top: 0.25rem;
      padding-bottom: 0;
    }
    a {
      color: var(--global-text-color);
      text-decoration: none;
      &:hover {
        color: var(--global-theme-color);
      }
    }
  }
}

.pagination {
  .page-item {
    .page-link {
      color: var(--global-text-color);
      &:hover {
        color: $black-color;
      }
    }
    &.active .page-link {
      color: $white-color;
      background-color: var(--global-theme-color);
      &:hover {
        background-color: var(--global-theme-color);
      }
    }
  }
}



// Distill

.distill {
  a:hover {
    border-bottom-color: var(--global-theme-color);
    text-decoration: none;
  }
}


// Projects

.projects {
  .grid-item {
    width: 250px;
    margin-bottom: 10px;
    a {
      color: black;
      text-decoration: none;
      &:hover {
        color: var(--global-theme-color);
      }
    }
  }
  .card img {
    width: 100%;
  }
}


// Publications

.publications {
  margin-top: 2rem;
  h1 {
    color: var(--global-theme-color);
    font-size: 2rem;
    text-align: center;
    margin-top: 1em;
    margin-bottom: 1em;
  }
  h2 {
    margin-bottom: 1rem;
    span {
      font-size: 1.5rem;
    }
  }
  h2.year {
    color: $grey-color-light;
    border-top: 1px solid $grey-color-light;
    padding-bottom: 3rem;
    margin-top: 2rem;
    margin-bottom: -2rem;
    text-align: right;
  }

  table td {
  font-size: 1rem;
  color: var(--global-text-color);
   }
  ol.bibliography {
    list-style: none;
    padding: 0;
    margin-top: 0;

    li {
      margin-bottom: 1rem;
      .abbr {
        height: 2rem;
        margin-bottom: 2rem;
        margin-top: 1rem;
        abbr {
          display: inline-block;
          background-color: var(--global-theme-color);
          padding-left: 1rem;
          padding-right: 1rem;
          a {
            color: white;
            &:hover {
              text-decoration: none;
            }
          }
        }
          .award {
          color: var(--global-theme-color) !important;
          border: 1px solid var(--global-theme-color);
        }
      }
      .title {
        font-weight: bolder;
      }
      .author {
        a {
          border-bottom: 1px dashed var(--global-theme-color);
          &:hover {
              border-bottom: 0px;
              text-decoration: none;
          }
        }
        > em {
          border-bottom: 1px solid;
          font-style: normal;
        }
        > span.more-authors {
          color: var(--global-text-color);
          border-bottom: 1px dashed var(--global-text-color);
          cursor: pointer;
          &:hover {
              color: var(--global-text-color);
              border-bottom: 1px dashed var(--global-text-color);
          }
        }
      }

      .links {
       a.btn {
         color: var(--global-text-color);
         border: 1px solid var(--global-text-color);
         padding-left: 1rem;
         padding-right: 1rem;
         padding-top: 0.25rem;
         padding-bottom: 0.25rem;
         &:hover {
           color: var(--global-theme-color);
           border-color: var(--global-theme-color);
         }
       }
     }
     .hidden {
       font-size: 0.875rem;
       max-height: 0px;
       overflow: hidden;
       text-align: justify;
       transition-property: 0.15s ease;
       -moz-transition: 0.15s ease;
       -ms-transition: 0.15s ease;
       -o-transition: 0.15s ease;
       transition: all 0.15s ease;

       p {
         line-height: 1.4em;
         margin: 10px;
       }
       pre {
         font-size: 1em;
         line-height: 1.4em;
         padding: 10px;
       }
     }
     .hidden.open {
       max-height: 100em;
       transition-property: 0.15s ease;
       -moz-transition: 0.15s ease;
       -ms-transition: 0.15s ease;
       -o-transition: 0.15s ease;
       transition: all 0.15s ease;
     }
     div.abstract.hidden {
       border: dashed 0px var(--global-bg-color);
     }
     div.abstract.hidden.open {
       border-color: var(--global-text-color);
     }
   }
 }
}

// Rouge Color Customization
figure.highlight {
  margin: 0 0 1rem;
}

pre {
  color: var(--global-theme-color);
  background-color: var(--global-code-bg-color);
  border-radius: 6px;
  padding: 6px 12px;
  pre, code {
    background-color: transparent;
    border-radius: 0;
    padding: 0;
  }
}

code {
  color: var(--global-theme-color);
  background-color: var(--global-code-bg-color);
  border-radius: 3px;
  padding: 3px 3px;
}


// Transitioning Themes
html.transition,
html.transition *,
html.transition *:before,
html.transition *:after {
  transition: all 750ms !important;
  transition-delay: 0 !important;
}


// Extra Markdown style (post Customization)
.post{
  .post-meta{
    color: $grey-color;;
    font-size: 0.875rem;
    margin-bottom: 0;
  }
  .post-tags{
    color: $grey-color;;
    font-size: 0.875rem;
    padding-bottom: 1rem;
    a {
      color: $grey-color;
      text-decoration: none;
      &:hover {
        color: var(--global-theme-color);
      }
    }
  }
  .post-content{
        blockquote {
          border-left: 5px solid var(--global-theme-color);
          padding: 8px;
      }
}
}

.post-tags {
  color: $grey-color;;
  font-size: 0.875rem;
  padding-top: 0.25rem;
}

.video-container { position: relative; padding-bottom: 56.25%; padding-top: 30px; height: 0; overflow: hidden; }

.video-container iframe, .video-container object, .video-container embed { position: absolute; top: 0; left: 0; width: 100%; height: 100%; }


// 20px gutter
.reduced-gutter.row {
  margin-right: -10px;
  margin-left: -10px;
}
.reduced-gutter {
  padding-right: 10px;
  padding-left: 10px;
}

a.off {
	color: inherit;
}

.note {
	font-size: 1.1rem;
	font-weight: 300;
  border-left: 0px solid;
	/* Museo */
}

.smallnote {
	font-size: 13px;
	font-weight: 300;
	line-height: 16px;
	/* Museo */
}

.hugespacer {
	height: 50px;
}

.bigspacer {
	height: 20px;
}

.spacer {
	height: 10px;
}

.smallspacer {
	height: 5px;
}

.spad-right {
	padding-right: 10px;
}

.pad-left {
	padding-left: 10px;
}

progress {
     /* Positioning */
     position: fixed;
     left: 0;
     top: 56px;
     z-index: 10;

     /* Dimensions */
     width: 100%;
     height: 2px;

     /* Reset the appearance */
     -webkit-appearance: none;
        -moz-appearance: none;
             appearance: none;

     /* Get rid of the default border in Firefox/Opera. */
     border: none;

     /* Progress bar container for Firefox/IE10  */
     background-color: transparent;

     /* Progress bar value for IE10  */
     color: var(--global-theme-color);
 }

 progress::-webkit-progress-bar {
     background-color: transparent;
 }

 progress::-webkit-progress-value {
     background-color: var(--global-theme-color);
 }

 progress::-moz-progress-bar {
     background-color: var(--global-theme-color);
 }

 .progress-container {
     width: 100%;
     background-color: transparent;
     position: fixed;
     top: 56px;
     left: 0;
     height: 5px;
     display: block;
 }

 .progress-bar {
     background-color: var(--global-theme-color);
     width: 0%;
     display: block;
     height: inherit;
 }
